/*------------------------------------------------------------------
[2.8 Cards / #leo_cards]
*/

.card {
    position: relative;
    .card-header{
        background: transparent;
    }

    &.timeline-card{
        &:before {
            content: "";
            position: absolute;
            top: 50%;
            left: 0;
            margin-left: .75rem;
            border: 8px solid $black;
            border-color: transparent $primary $primary transparent;
            -webkit-box-sizing: border-box;
            box-sizing: border-box;
            -webkit-transform-origin: 0 0;
            transform-origin: 0 0;
            @include transform(rotate(135deg));
            @include box-shadow($box-shadow-soft);
        }
    }

    &.left-timeline-card{
        &:after{
            content: "";
            position: absolute;
            top: 50%;
            right: 0;
            margin-right: -5px;;
            border: 8px solid $black;
            border-color: transparent $primary $primary transparent;
            -webkit-box-sizing: border-box;
            box-sizing: border-box;
            -webkit-transform-origin: 0 0;
            transform-origin: 0 0;
            @include transform(rotate(-45deg));
            @include box-shadow($box-shadow-soft); 
        }
    }
}

.customer-testimonial {
    .content{
        &:before {
            content: "";
            position: absolute;
            top: 25px;
            left: 0;
            margin-left: .75rem;
            border: 8px solid $black;
            border-color: transparent $primary $primary transparent;
            -webkit-box-sizing: border-box;
            box-sizing: border-box;
            -webkit-transform-origin: 0 0;
            transform-origin: 0 0;
            @include transform(rotate(135deg));
            @include box-shadow($box-shadow-soft);
        }
    }
}

.profile-page {
    .card-profile {
        margin-top: -150px;

        .card-profile-image {
            position: relative;

            img {
                max-width: 180px;
                @include border-radius($border-radius);
                transform: translate(-50%,-30%);
                position: absolute;
                left: 50%;
                transition: $transition-base;

                &:hover {
                    transform: translate(-50%, -33%);
                }
            }
        }

        .card-profile-stats {
            padding: 1rem 0;

            > div {
                text-align: center;
                margin-right: 1rem;
                padding: .875rem;

                &:last-child {
                    margin-right: 0;
                }

                .heading {
                    display: block;
                    font-size: 1.1rem;
                    font-weight: bold;
                }
                .description {
                    font-size: .875rem;
                    color: $gray-500;
                }
            }
        }

        .card-profile-actions {
            padding: .875rem;
        }

        @include media-breakpoint-down(xs) {
            .card-profile-actions {
                margin-top: 110px;
            } 
        }

        @include media-breakpoint-between(sm, md) {
            .card-profile-stats {
                margin-top: 30px;
            }
        }
    }
}

.card-footer{
    background-color:transparent;
}

// Card with blockquotes

.card {
    .card-blockquote {
        position: relative;
        padding: 2rem;

        .svg-bg {
            display: block;
            position: absolute;
            width: 100%;
            height: 95px;
            top: -94px;
            left: 0;
        }
    }
}

// Animated cards

.card-lift-hover {
    &:hover {
        transform: translateY(-20px);
        @include transition($transition-base);
    }
}


